html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #ebebeb;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-md {
  max-width: 96rem;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 75rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
  padding: 0;
}

.seccion {
  margin: 3.5rem 0;
}

.bd-container {
  max-width: 968px;
  width: calc(100% - 3rem);
  margin-left: 2.4rem;
  margin-right: 2.4rem;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

p {
  font-family: "Poppins", sans-serif;
  font-size: 1.5rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  text-align: center;
  margin: 5rem 0;
}

.slogan {
  text-align: center;
  margin: 5rem 0;
}

.descripcion-pagina {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

.header {
  background: #06439E;
  height: 120vh;
  padding-top: 2rem;
  padding-bottom: 7rem;
  transition: 300ms;
}
@media (min-width: 769px) {
  .header {
    height: 100vh;
  }
}
.header .contenido-header {
  display: flex;
  min-height: 100%;
  flex-direction: column;
  justify-content: space-between;
}
.header .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .nav .logo {
  color: #ebebeb;
  font-size: 2.9rem;
  font-weight: 600;
  transition: 300ms;
}
.header .nav .logo:hover {
  color: #EB4D38;
}
@media screen and (max-width: 768px) {
  .header .nav .menu {
    position: fixed;
    width: 60%;
    top: 6.3rem;
    right: 0;
    height: 100vh;
    background: linear-gradient(to right bottom, #689ff1, rgba(255, 255, 255, 0.3));
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    padding: 2.4rem;
    z-index: 10;
    transition: 300ms;
    border-top-left-radius: 1.6rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
  }
  .header .nav .menu.mostrar {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
.header .nav .link {
  margin-bottom: 2.4rem;
  color: #fff;
  background: linear-gradient(currentColor, currentColor);
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-size: 0% 2px;
  transition: 300ms;
}
.header .nav .link:hover {
  color: #EB4D38;
  background-size: 100% 2px;
}
@media (min-width: 769px) {
  .header .nav .link {
    display: initial;
    margin-bottom: 0;
    margin-left: 4.8rem;
  }
}
.header .nav .toggle {
  font-size: 3rem;
  cursor: pointer;
  color: #ebebeb;
}
@media (min-width: 769px) {
  .header .nav .toggle {
    display: none;
  }
}
.header .header-data {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 769px) {
  .header .header-data {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
.header .header-data .title {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 769px) {
  .header .header-data .title {
    align-items: flex-start;
  }
}
.header .header-data .title h1 {
  font-size: 4rem;
  color: #ebebeb;
  text-align: center;
}
.header .header-data .title h1 span {
  color: #2273eb;
}
@media (min-width: 769px) {
  .header .header-data .title h1 {
    font-size: 5rem;
    text-align: initial;
    max-width: 100%;
  }
}
.header .header-data .title .button {
  background-color: #ee6a58;
  /* background-color: v.$rojo; */
  color: #ebebeb;
  padding: 1.5rem 1.2rem;
  border-radius: 1.2rem;
  transition: 300ms;
  display: flex;
  align-items: center;
  font-size: 1.8rem;
  font-weight: 500;
}
.header .header-data .title .button:hover {
  padding-right: 2rem;
  box-shadow: 3px 0 24px 8px #ee6a58;
}
.header .header-data .img {
  order: -1;
}
@media screen and (max-width: 768px) {
  .header .header-data .img {
    width: 30rem;
  }
}
.header .header-social {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: initial;
}
@media (min-width: 769px) {
  .header .header-social {
    justify-content: flex-start;
  }
}
.header .header-social span {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  border-left: 1px solid #ebebeb;
  color: #ebebeb;
  margin-right: 2rem;
  padding: 1.6rem 0.4rem;
}
.header .header-social .icon {
  margin-right: 1.6rem;
  width: 3.9rem;
  height: 3.9rem;
  font-size: 3rem;
  color: #06439E;
  background-color: #ebebeb;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: 300ms;
}
.header .header-social .icon:hover {
  background: transparent;
  color: #ebebeb;
  transform: scale(1.3);
}

.btntop {
  position: fixed;
  width: 4rem;
  height: 4rem;
  right: 2rem;
  bottom: 2rem;
  background-color: #EB4D38;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  border-radius: 0.5rem;
  transition: 300ms;
  transform: translateY(250%);
}
.btntop .icon {
  font-size: 2.4rem;
  font-weight: 800;
  color: #ebebeb;
  transition: 300ms;
}
.btntop:hover {
  transform: scale(1.1);
}
.btntop:hover .icon {
  transform: translateY(-0.2rem) scale(1.1);
}
.btntop.mostrar {
  transform: translateY(0);
}

.beneficios {
  padding-top: 4.3rem;
  padding-bottom: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #ebebeb;
}
@media (min-width: 769px) {
  .beneficios {
    padding-bottom: 4rem;
  }
}
@media (min-width: 1024) {
  .beneficios {
    padding-left: 0;
    padding-right: 0;
  }
}
.beneficios h2 {
  text-align: center;
  color: #ee6a58;
}
@media (min-width: 769px) {
  .beneficios h2 {
    font-size: 5rem;
  }
}
.beneficios .contenedor-beneficios {
  margin-top: 4rem;
}
@media (min-width: 769px) {
  .beneficios .contenedor-beneficios {
    margin-top: 5rem;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
  }
}
.beneficios .contenedor-beneficios .beneficio {
  text-align: center;
  border-radius: 1.3rem;
  box-shadow: 0 2px 6px #858c99;
  padding: 1rem;
  margin: 0 6rem 3rem 6rem;
  height: 28rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 250ms;
}
.beneficios .contenedor-beneficios .beneficio:hover {
  box-shadow: 3px 0px 24px #5191ef;
  background-color: #2273EB;
}
.beneficios .contenedor-beneficios .beneficio:hover .icono {
  color: #ebebeb;
}
.beneficios .contenedor-beneficios .beneficio:hover .contenido-beneficio h3 {
  color: #ebebeb;
}
@media (min-width: 769px) {
  .beneficios .contenedor-beneficios .beneficio {
    margin: 0;
    padding: 1rem;
    width: 95%;
    height: 30rem;
  }
}
.beneficios .contenedor-beneficios .beneficio .icono {
  font-size: 10rem;
  color: #2273EB;
}
@media (min-width: 769px) {
  .beneficios .contenedor-beneficios .beneficio .icono {
    font-size: 15rem;
  }
}
.beneficios .contenedor-beneficios .beneficio .contenido-beneficio h3 {
  font-size: 3rem;
  color: #2273EB;
}
@media (min-width: 769px) {
  .beneficios .contenedor-beneficios .beneficio .contenido-beneficio h3 {
    font-size: 3rem;
  }
}

.incluye {
  padding-bottom: 4rem;
}
.incluye h2 {
  text-align: center;
  color: #06439E;
  font-size: 5rem;
}
.incluye .contenedor-incluye {
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 769px) {
  .incluye .contenedor-incluye {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.incluye .contenedor-incluye .card {
  padding: 4rem 1rem;
  text-align: center;
  min-height: 30rem;
  box-shadow: 3px 0 20px #858c99;
  margin: 0 4rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  border: 2px solid #06439E;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 300ms;
}
.incluye .contenedor-incluye .card:hover {
  box-shadow: 3px 0 20px #393939;
}
@media (min-width: 769px) {
  .incluye .contenedor-incluye .card {
    margin: 0 0 2rem 0;
  }
}
.incluye .contenedor-incluye .card .button {
  background-color: #ee6a58;
  text-align: center;
  /* background-color: v.$rojo; */
  color: #ebebeb;
  padding: 1.5rem 1.2rem;
  border-radius: 1.2rem;
  transition: 300ms;
  font-size: 1.8rem;
  font-weight: 500;
  margin-top: 2rem;
}
.incluye .contenedor-incluye .card .button:hover {
  padding-right: 2rem;
  box-shadow: 3px 0 24px 8px #ee6a58;
}
@media (min-width: 769px) {
  .incluye .contenedor-incluye .card .button {
    align-self: center;
    padding: 1.5rem 3rem;
  }
  .incluye .contenedor-incluye .card .button:hover {
    padding-right: 4rem;
  }
}
.incluye .card-data h3 {
  color: #ee6a58;
}
.incluye .card-data p {
  font-weight: 500;
}
.incluye .card-descripcion {
  align-self: center;
}
.incluye .card-descripcion ul {
  text-align: initial;
  color: #043072;
}
.incluye .card-descripcion ul li {
  margin-bottom: 0.7rem;
  position: relative;
}
.incluye .card-descripcion ul li::before {
  content: "";
  width: 1rem;
  height: 1rem;
  position: absolute;
  top: 0.38rem;
  left: -2rem;
  background-color: #ee6a58;
  border-radius: 40%;
  box-shadow: 0px 0 10px #ee6a58;
}

.contacto {
  padding: 4rem 0 2.4rem 0;
  background: #06439E;
}
@media (min-width: 769px) {
  .contacto {
    background: none;
  }
}
.contacto .contenedor-contacto {
  display: grid;
  gap: 2.4rem;
  grid-template-columns: repeat(1, 1fr);
}
.contacto .contenedor-contacto h2,
.contacto .contenedor-contacto p {
  text-align: center;
  color: #ebebeb;
}
.contacto .contenedor-contacto p {
  margin-bottom: 3.2rem;
}
@media (min-width: 769px) {
  .contacto .contenedor-contacto {
    background-color: #06439E;
    border-radius: 1.5rem;
    padding-top: 3rem;
    padding-bottom: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
}
.contacto .contenido-contacto {
  flex: 0.8;
}
.contacto .contenido-contacto .formulario {
  background: #ebebeb;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  padding-top: 3rem;
  border-radius: 1rem;
}
@media (min-width: 769px) {
  .contacto .contenido-contacto .formulario {
    max-width: 80%;
    margin: 0 auto;
  }
}
.contacto .contenido-contacto .formulario input[type=text],
.contacto .contenido-contacto .formulario input[type=email] {
  width: 100%;
  border: none;
  outline: none;
  background-color: #fff;
  font-size: 1.6rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  padding: 1rem 2rem;
}
.contacto .contenido-contacto .formulario textarea {
  width: 100%;
  border: none;
  outline: none;
  background-color: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
}
.contacto .contenido-contacto .button {
  margin-top: 3rem;
  background-color: #ee6a58;
  /* background-color: v.$rojo; */
  color: #ebebeb;
  padding: 1.5rem 1.2rem;
  border-radius: 1.2rem;
  transition: 300ms;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  border: none;
  outline: none;
  cursor: pointer;
}
.contacto .contenido-contacto .button:hover {
  padding-right: 2rem;
  box-shadow: 3px 0 24px 8px #ee6a58;
}
.contacto .tagline {
  color: #ebebeb;
  font-size: 2.5rem;
  flex: 0.2;
  margin: 0;
  margin-top: 1rem;
}
.contacto .contacto-whatsapp {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  align-items: center;
  gap: 1rem;
}
.contacto .contacto-whatsapp span {
  font-size: 1.8rem;
  color: #ebebeb;
  transition: 300ms;
  background: linear-gradient(currentColor, currentColor);
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-size: 0% 2px;
}
.contacto .contacto-whatsapp span:hover {
  color: #EB4D38;
  background-size: 100% 2px;
}
.contacto .contacto-whatsapp .icon {
  margin-right: 0;
  width: 5rem;
  height: 5rem;
  font-size: 4rem;
  color: #ebebeb;
  background-color: #25D366;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: 300ms;
}
.contacto .contacto-whatsapp .icon:hover {
  background: transparent;
  color: #ebebeb;
  transform: scale(1.3);
}

.footer {
  padding-top: 6.4rem;
  padding-bottom: 3.2rem;
}
.footer .contenedor-footer {
  display: grid;
  gap: 2.4rem;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
}
@media screen and (max-width: 768px) {
  .footer .contenedor-footer {
    margin-left: 2.4rem;
    margin-right: 2.4rem;
  }
}
@media (min-width: 769px) {
  .footer .contenido-footer {
    text-align: center;
  }
}
.footer .contenido-footer .logo-footer {
  color: #06439E;
  font-size: 1.8rem;
  transition: 200ms;
}
.footer .contenido-footer .titulo-footer {
